let localLoggedinUser = null;
localLoggedinUser = sessionStorage.loggedinUser
  ? JSON.parse(sessionStorage.loggedinUser)
  : { _id: 'u100', fullname: 'Guest', imgUrl: './imgs/guest.jpg' };

const initialState = {
  loggedInUser: localLoggedinUser,
  users: [],
  isLogin: false,
};

export function userReducer(state = initialState, action = {}) {
  switch (action.type) {
    case 'SET_USER':
      return {
        ...state,
        loggedInUser: action.user,
        // isLogin:!state.isLogin
      };
    case 'REMOVE_USER':
      return {
        ...state,
        users: state.users.filter((user) => user._id !== action.userId),
      };
    case 'SET_USERS':
      return { ...state, users: action.users };
    case 'SET_SCORE':
      return {
        ...state,
        loggedInUser: { ...state.loggedInUser, score: action.score },
      };
    default:
      return state;
  }
}
